.historia-container {
  padding: 20px;
  font-family: Arial, sans-serif;
  line-height: 1.6;
}

h1 {
  font-size: 2rem;
  color: #003366;
  margin-bottom: 20px;
  text-align: center;
}

p {
  font-size: 1rem;
  color: #333;
  margin-bottom: 15px;
}

strong {
  font-weight: bold;
}




@media (max-width: 768px) {
  h1 {
    font-size: 1.5rem; 
  }

  p {
    font-size: 0.9rem; 
  }

  .historia-container {
    padding: 15px; 
  }
}


@media (min-width: 768px) and (max-width: 1024px) {
  h1 {
    font-size: 1.8rem; 
  }

  p {
    font-size: 1rem; 
  }

  .historia-container {
    padding: 20px; 
  }
}


@media (min-width: 1024px) {
  h1 {
    font-size: 2rem; 
  }

  p {
    font-size: 1.1rem; 
  }

  .historia-container {
    padding: 25px; 
  }
}


@media (min-width: 1500px) {
  h1 {
    font-size: 2.5rem; 
  }

  p {
    font-size: 1.2rem; 
  }

  .historia-container {
    padding: 30px; 
  }
}
